*{
  box-sizing:border-box;
  margin:0;
  padding:0;
  font-family: 'Gotham, 'Helvetica 'Neue', Helvetica, Arial, sans-serif;
  font-size:10px;
  text-transform: capitalize;
  line-height: 1.4;
}

h3{
  color:#575b5b;
  font-weight:bold;
}

h4{
  text-transform:uppercase;
}

h6{
  text-decoration: underline;
}

a{
  color:rgb(13, 79, 83);
  text-decoration: none;
  margin:2px;
}

ul{
  list-style: none;
}

.brew{
  padding:20px 10px;
  margin-bottom:6px;
  border-radius:3px;
}
